import React,{useEffect,useState} from 'react'
import Axios from '../../helpers/axios'
import { Table,Button ,Col,Container,Row,Modal} from 'react-bootstrap';

const LastMonth = () => {
const[data,setData] = useState(null)

useEffect(() => {
        (async function() {
            try {
                const {data:data} = await Axios.get("/get_last_month")
                setData(data['data']);
            } catch (e) {
                console.error(e);
            }
        })();
    },[]);
    const ths = ["עדכן","סטטוס","תמונה","תאריך","המוצר","הספק","עוד מידע","מחיר","מחק מהרשימה"]
    const confirmProduct =async (event,id,status) => {
        event.preventDefault()
        try {
            const response = await Axios.put("/update_product_status",{id,status})
            if(response.data.msg == 'success'){
                let dataUpdate = data.map(item => {
                    if(item.id == id ){
                        item.confirm = !item.confirm
                    } 
                    return item
                })
                setData(dataUpdate);
            }
        } catch (e) {
            console.error(e);
        }
    }
    const deleteItem = async (e,id) => {
        e.preventDefault()
        try {
            const response = await Axios.post("/delete_product",{id})
            if(response.data.msg == 'success'){
                let dataUpdate = data.filter(item => item.id != id)
                setData(dataUpdate);
            }
        } catch (e) {
            console.error(e);
        }
    }
    
    if (data === null) {
        return (
          <div>Still loading...</div>
          );
      }

    return(
        <Container  className ="p-5">
        <div className ="table table-responsive">
        <Table className ="table">
            <thead>
            <tr>
                {ths.map(th => <th  scope="col" className="border-1">{th}</th>)}
            </tr>
            </thead>
            <tbody>
            {data.map(item =>{
                return (
            <tr>    
                <td  scope="col" className="border-1"><Button className ={item.confirm ? "btn btn-success" : "btn btn-danger"} onClick = {(e) => confirmProduct(e,item.id,item.confirm )}>{item.confirm ? "בטל זיכוי": "אשר את הזיכוי" }</Button></td>
                <td  scope="col" className="border-1">{item.confirm ? "זוכה" : "טרם זוכה"}</td>
                <td  scope="col" className="border-1">{ item.fileName ? ( <div><a   href ={`http://ec2-34-242-121-102.eu-west-1.compute.amazonaws.com:5000/public/${item.fileName}`}>צפה בתמונה </a></div>) : (<div>אין קובץ</div>)}</td>
                <td  scope="col" className="border-1">{item.created_at.replace("T"," ").replace("Z","").replace(".000","")}</td>
                <td  scope="col" className="border-1">{item.product_name}</td>
                <td  scope="col" className="border-1">{item.provider}</td>
                <td  scope="col" className="border-1">{item.more_data}</td>
                <td  scope="col" className="border-1">{item.product_price} ש"ח</td>
                <td  scope="col" className="border-1"><Button onClick ={(e) => deleteItem(e,item.id)}>מחק פריט</Button></td>
                </tr>
                 )   
                })
            }
            </tbody>
        </Table>
        </div>
        </Container>
        )
}

export default LastMonth