import FormComponent from "../../components/form/form.component";
import React,{useState,useEffect} from "react";
import Axios from '../../helpers/axios';
const FormPromotional = () => {
    const [state, setState] = useState({ provider_name: "", more_info: "" ,product_price:"",product_name:''});
    const [options, setOptions] = useState(null);
    const inputs = [{label:'מק"ט/שם המוצר',name:'product_name',type:'text'},{label:'מחיר',name:'product_price',type:'number'},{label:'עוד מידע',name:'more_info',type:'text'},{label:'הוסף תמונות',name:'file',type:'file'}]
    const title = 'הוסף מוצר לזיכוי'
    const handleChange = (event) => {
        const { name, value } = event.target;
        if(name == 'file'){
            setState(prevState => ({ ...prevState, [name]: event.target.files[0] }));
        }else{
            setState(prevState => ({ ...prevState, [name]: value }));
        }
    }
    useEffect(() => {
        (async () => {
            try {
                const {data:{providers:providers}} = await Axios.get("/get_providers")
                setOptions(providers.map(item => item.name));
            } catch (e) {
                console.error(e);
            }
        })();
    },[]);
 
    const handleSubmit = async (event) => {
        event.preventDefault();
        const formData = new FormData();
        for(const[key,value] of Object.entries(state)){
            formData.append(key, value)
        }
        try{
            await Axios.post('/add_product',formData).then(response => {
                alert(response.data.msg)
            })
        }catch(err){
            console.log(err)
        }
    }
    return(
        <FormComponent inputs = {inputs}  handleChange ={handleChange} handleSubmit = {handleSubmit} title ={title} selectOptions = {options}/>
    )


}

export default FormPromotional